import BiuniqueForm from '@/components/biunique/BiuniqueForm'
import { getOptionsBatch, getOptionsList } from '@/api/common'
import { targetLevelList, paperLevelWrap } from '@/config/mock'
export default {
  components:{
    BiuniqueForm
  },
  data () {
    return {
      formConfig:[
        { label:'论文名称', prop:'title', type:'input', required:true },
        { label:'论文领域', prop:'domain', type:'select', options:[], required:true },
        { label:'姓名', prop:'surname', type:'input', required:true },
        { label:'手机', prop:'phone', type:'input', required:true },
        { label:'邮箱地址', prop:'email', type:'input', required:true, formAttr: { type: "email" } },
        { label:'论文语言', prop:'language', type:'radio', options:[{name:'英文', value:1}, {name:'中文', value:2}], required:true },
        { label:'上传论文', prop:'document', type:'upload', required:true },
        { label:'目标期刊级别', prop:'targetLevel', type:'select', options:Object.keys(targetLevelList).map((key)=>({name:targetLevelList[key], value:+key})) },
        { label:'职称', prop:'position', type:'select', options:[]},
        { label:'学校/单位', prop:'organization', type:'input', required:true },
        { label:'最高学历', prop:'education', type:'input', placeholder:'研一，研二', required:true, iscCustoms:true },
        { label:'专业', prop:'speciality', type:'input', placeholder:'本科、硕士、博士对应', required:true, iscCustoms:true },
        { label:'英语水平', prop:'englishLevel', type:'input', placeholder:'四级，550', required:true, iscCustoms:true },
        { label:'论文研究方向', prop:'paperDirection', type:'input', placeholder:'CAR-T细胞免疫疗法在癌症治疗中的进展', iscCustoms:true, required:true },
        { label:'论文级别', prop:'paperLevel', type:'select', placeholder:'SCI', tips:'请标注SCI,SSCI,CSSCI,EI，CPCI,普刊，EIE论文，毕业论文', iscCustoms:true, options:Object.keys(paperLevelWrap).map(p => {return {name:paperLevelWrap[p], value:+p}}), required:true },
        { label:'已发表论文', prop:'paperStatus', type:'input', placeholder:'2篇EI国际会议论文', tips:'请标注SCI,SSCI,CSSCI,EI，CPCI,EI会议，普刊', iscCustoms:true, required:true },
        { label:'培训目标', prop:'trainTerget', type:'input', placeholder:'申博，海外', required:true, tips:'请标注保研，申博（国内/海外），毕业，职位晋升等', iscCustoms:true },
        { label:'论文发表时间', prop:'trainCycle', type:'input', placeholder:'2022年6月', required:true, iscCustoms:true, tips:'论文最迟发表时间' },
        { label:'推荐码', prop:'inviteCode', type:'input' },
        { label:'备注', prop:'remark', type:'input' },
      ]
    }
  },
  created () {
    this.getOptionsHandle()
    this.getPosition()
  },
  methods:{
    async getOptionsHandle() {
      const {data:{code, data:{domains} }} = await getOptionsBatch('domains')
      if (code === 0) {
        this.$set(this.formConfig, 1, {...this.formConfig[1], options:domains})
      }
    },
    async getPosition() {
      const {data} = await getOptionsList('userPosition')
      if(data.code === 0){
        let params = data.data.options.map(item => {
          return {
            ...item,
            value:item.name,
          }
        })
        this.$set(this.formConfig, 8, {...this.formConfig[8], options:params})
      }
    },
  },
  head () {
    return {
      title: '科研论文一对一'
    }
  }
}
