import { submitTrade, uploadTrade, getTradeCoupon } from '@/api/biunique'
import reg from '@/config/reg.config'
import config from "@/config/common.config"
import BiuniqueAgree from './BiuniqueAgree'
import CountrySelect from '@/components/Common/CountrySelect'
export default {
  components:{
    BiuniqueAgree,
    CountrySelect
  },
  props:{
    formConfig:{
      type:Array,
      required:true
    },
    otherConfig:{
      type:Object,
      required:true
    },
  },
  data () {
    return {
      form:{
        countryCode:86,
        customs:{}
      },
      showPicker:false, //展开选择器
      activeFormConfig:{

      }, //选择器下拉
      pickerIndex:0, //选择器的值
      checked:false,
      config,
      isSuccess:false,
      cardList:[], //优惠券列表
      cardShow:false,
      radio: null, //优惠券选择
      couponValue: null, //优惠券价格
      agreeShow:false
    }
  },
  computed:{
    columns() {
      return this.activeFormConfig.options || []
    }
  },
  mounted() {
    this.getTradeCoupon()
  },
  methods:{
    async getTradeCoupon() {
      const { data } = await getTradeCoupon()
      if (data.code === 0){
        this.cardList = data.data.cardList
      }
    },
    async uploadFileHandle(file){
      let formData = new FormData()
      formData.append("file", file.file)
      const {data} = await uploadTrade(formData)
      if (data.code === 0) {
        this.$toast.success("上传成功")
        this.$set(this.form, 'document', data.data)          
      }
    },
    //选择器选中
    onConfirm(value) {
      this.$set(this.form, this.activeFormConfig.prop, value.value)
      this.showPicker = false
      this.pickerIndex = 0
    },
    //打开选中器
    pickerShow (item) {
      this.activeFormConfig = item
      this.pickerIndex = !this.form[item.prop] ? 0 : this.activeFormConfig.options.findIndex(p => p.value === this.form[item.prop])
      this.showPicker = true
    },
    changeFormValue(item) {
      const file = item.options.find(p=>p.value === this.form[item.prop])
      return file && file.name ? file.name :null
    },
    changeRaido(value){
      return value = value + 1
    },
    couponClick() {
      if(this.cardList.length === 0) {
        return
      }
      this.cardShow = true
    },
    //优惠券选择
    handleClick(){
      if(this.radio === 0) {
        this.couponValue = 0
      } else {
        const index = this.cardList.findIndex(p=>p.card === this.radio)
        this.couponValue = this.cardList[index].money
      }
      this.cardShow = false
    },
    //表单提交
    async onSubmit() {
      const { email:regEmail} = reg
      const { email, document } = this.form
      //验证手机
      //if (!regPhone.test(phone)) return this.$toast.fail('请输入正确手机号码!')
      //验证邮箱
      if (email && !regEmail.test(email)) return this.$toast.fail('请输入正确邮箱地址!')
      if (!document && this.otherConfig.type === 11 ) return this.$toast.fail('请上传论文!')
      //if(!this.checked) return this.$toast.fail('请仔细阅读协议并同意协议!')
      let params = {
        ...this.form,
        productId:(this.otherConfig.type%10),
        service:+(this.$route.params.id)
      }
      if(this.radio){
        params.card = this.radio
      }
      if(+(this.$route.query.categoryId)) {
        params.categoryId = +(this.$route.query.categoryId)
      }
      this.formConfig.forEach(element => {
        if(element.iscCustoms){
          params.customs[element.prop] = params[element.prop]
        }
      })
      const { data } = await submitTrade(params)
      if (data.code === 0) {
        this.$toast.success(`提交成功!`)
        this.isSuccess = true
      }
    },
  }
}
